import { Injectable } from '@angular/core';
import { RxButtonBarAlignment, RX_BUTTON_BAR } from './button-bar.types';
import { get } from 'lodash';

@Injectable()
export class RxButtonBarService {
  getAlignClass(alignment: RxButtonBarAlignment): string {
    return get(RX_BUTTON_BAR.alignmentOptions, [alignment, 'cls'], '');
  }
}
